import React from 'react';
import "../node_modules/bootstrap/dist/css/bootstrap.min.css"; //allows us to use bootstrap function
import "../node_modules/bootstrap/dist/js/bootstrap.bundle";  // to use that dropdown in output as without js dropdown cannot be used
import Home from "./Home";
import About from "./About";
import Service from "./Service";
import Contact from "./Contact";
import Navbar from "./Navbar";

import {Switch,Route,Redirect} from "react-router-dom";
import Footer from './Footer';
export default function App() {
    return (
       <>
       <Navbar />
     <Switch>
     <Route exact path ="/"  component ={Home}/>
     <Route exact path ="/about"  component ={About}/>
     <Route exact path ="/service"  component ={Service}/>
     <Route exact path ="/contact"  component ={Contact}/>
     <Redirect to  ="/"/>
     
    </Switch>
    <Footer/>
       </>

    )
}
